<style scoped>
    p.switch {
        font-size: 13px;
        margin: 0;
    }
    td {
        position: relative;
    }
    button {
        height: 22px;
        line-height: 0;
    }
    button:disabled {
        opacity: 0.4;
        cursor: initial;
    }
    button.reverse {
        width: 22px;
        height: 22px;
        padding: 0;
        margin-top: -2px;
    }
    button.reverse img {
        width: 13px;
        vertical-align: middle;
        height: 13px;
    }    
    button.tfs4jira-mapping-row-operation {
        position: absolute;
        right: 0;
        margin: 0;
        top: 50%;
        padding: 0px;
        transform: translateY(-11px);
    }
</style>

<template>
    <tr v-if="mapping.jiraTitle !== undefined && mapping.tfsTitle !== undefined" class="tfs4jira-mapping-row" style="background-color: rgb(255, 255, 255);">
        <td class="tfs4jira-mapping-row-left">                    
            <div class="tfs4jira-mapping-value-text">
                <div class="tfs4jira-mapping-value-text-label">{{mapping.jiraTitle}}</div>                    
            </div>
        </td>

        <td class="tfs4jira-mapping-row-direction"><div class="tfs4jira-mapping-row-direction-both" title=""> </div></td>

        <td class="tfs4jira-mapping-row-right">            
            <div class="tfs4jira-mapping-value-text">
                <button :disabled="mapping.duringProcessing" @click="remove(mapping)" class="tfs4jira-mapping-row-operation tfs4jira-merged-mapping-remove tfs4jira-rounded-button" title="Remove">
                    <img :src="getImagePath('delete-icon.svg')">
                </button>                                                    
                {{mapping.tfsTitle}}
                <button v-show="mapping.tfsValue && mapping.tfsValue.hasInvertedVersion" :disabled="mapping.duringProcessing" @click="switchMapping(mapping)" class="tfs4jira-rounded-button reverse" :title="switchTitleText()">
                    <img :src="getImagePath('reverse-icon.svg')">
                </button>
                </div>                
            </div>                
        </td>
    </tr>
</template>

<script>
    import contextPath from '../context-path';
    export default {
        props: ['mapping', 'remove', 'switchMapping'],
        data() {
            return {
                contextPath: contextPath
            }
        },
        methods: {
            switchTitleText() {
                return "Swtich to " + this.mapping.invertedTitle;
            },
            getImagePath(imageFileName) {
                return contextPath + "/Content/Images/" + imageFileName;
            }
        }
    }
</script>



