import Vue from 'vue'
import LinkMapping from './components/LinkMapping';
import ProfileHealthComponent from './components/ProfileHealthComponent';
import WizardSideComponent from './components/WizardSideComponent';
import HealthStatusIndicator from './components/HealthStatusIndicator';
import MessageBox from './components/MessageBox';

Vue.config.productionTip = false

Array.prototype.forEach.call(document.querySelectorAll('.vue-container'), (container) => {
	new Vue({
	  el: '#' + container.getAttribute("id"),
	  components: { 
	  	LinkMapping,
	  	WizardSideComponent,
	  	HealthStatusIndicator,
	  	ProfileHealthComponent,
	  	MessageBox
	  },
	})
});


