let shared = {
    currentWizardStep: '',
    jiraDirty: false,
    tfsDirty: false,
    messageBoxItems: [],
    componentNotification: null,
    notifyComponent: function(componentTag) {
    	 this.componentNotification = {component: componentTag};
    },
	addMessageBoxItem: function(title, message) {
		this.messageBoxItems.push({title: title, message: message});
	}
};

window.Spartez = window.Spartez || {};
window.Spartez.Shared = shared;

export default shared;



