import axios from 'axios';
import Vue from 'vue'

export default class LinkMappingService  {
	constructor(backendUrl, http) {
		this.http = http;
		this.backendUrl = backendUrl;
	}

	/*
	 * Fetch data on component mount from Rest Api. Enrich it and return to component
	 */
	getData() {
		var self = this;

		return new Promise(function(resolve, reject){
			self.http.get(self.backendUrl + '/mappings/links/availableValues').then(
				(availableValues) => {
					self.http.get(self.backendUrl + '/mappings/links').then(
						(mappings) => {
							resolve(self.shapeDataFromWebService(
								availableValues.data,
								mappings.data.LinkTypesMappings
							));
						},
						(err) => {
							reject(err);
						}
					);
				},
				(err) => {
					reject(err);
				}
			);
		});
	}

	shapeDataFromWebService(availableValues, mappings) {
		function createItem(kind, title, hasInvertedVersion, key, isInverted) {			
			return {
				kind: kind,
				title: title,
				invertedTitle: title.split("/").reverse().join(" / "),
				id: key + '_' + (isInverted),
				key: key,
				inverted: isInverted,
				hasInvertedVersion: hasInvertedVersion,
				visible: true
			}
		};

		var items = {};

		Array.prototype.forEach.call(availableValues.JiraValues, el => {
			let item = createItem(				
				'JIRA',
				el.LabelA + ' / ' + el.LabelB,
				el.LabelA !== el.LabelB,
				el.JiraLinkType,
				false
			);
			items[item.id] = item;
		});

		// Create items for TFS types. For each item we need to create inverted version
		Array.prototype.forEach.call(availableValues.TfsValues, el => {
			let item = (createItem(
				'TFS',
				el.LabelA + ' / ' + el.LabelB,
				el.LabelA !== el.LabelB,
				el.TfsLinkType,
				false
			));

			items[item.id] = item;

			if (el.LabelA !== el.LabelB) {
				let invertedItem = (createItem(
					'TFS',
					el.LabelB + ' / ' + el.LabelA,
					el.LabelA !== el.LabelB,
					el.TfsLinkType,
					true
				));
				items[invertedItem.id] = invertedItem;
			}
		});

		return this.hideListItemsAndEnrichMappings(mappings, items);
	}

	/*
	 * Hide jira / tfs item if mapping for this item is already created
	 * Also enhance informations about the mapped link
	 */
	hideListItemsAndEnrichMappings(mappings, items) {		
		Array.prototype.forEach.call(mappings, mapping => {
			// For a mapping find related JiraValues and TfsValues
			Array.prototype.forEach.call(Object.values(items), item => {
				if (item.key === mapping.JiraLinkType) {
					mapping.jiraValue = item;
				} else if (item.key === mapping.TfsLinkType && mapping.IsLinkDirectionInverted === item.inverted) {
					mapping.tfsValue = item;
				}
			});
			mapping.duringProcessing = false;

			if (mapping.jiraValue === undefined) {
				mapping.jiraTitle = mapping.JiraLinkType;
			} else {
				mapping.jiraId = mapping.jiraValue.id;				
				mapping.jiraTitle = mapping.jiraValue.title;
			}

			if (mapping.tfsValue === undefined) {
				mapping.tfsTitle = mapping.TfsLinkType;
			} else {
				mapping.tfsTitle = mapping.tfsValue.title;
				mapping.tfsId  = mapping.tfsValue.id;
				mapping.invertedTitle = mapping.tfsValue.invertedTitle;
			}
		});

		mappings = Array.prototype.sort.call(mappings, function(a, b){			
			return a.jiraTitle.toString() > b.jiraTitle.toString() ? -1 : 1;
		});

		Array.prototype.forEach.call(Object.values(items), item => {
			var foundMappings = Array.prototype.filter.call(
				mappings, 
				(mapping) => mapping.jiraId === item.id || mapping.tfsId === item.id || (mapping.tfsValue !== undefined && mapping.tfsValue.key == item.key)
			);
			// visible only if its not inverted version and not yet mapped
			item.visible = foundMappings.length === 0 && item.inverted == false;
		});

		return {
			available: items,
			mappings: mappings
		};
	}

	switchTfsMapping(mappingRow, mappings, availableValues) {
		var self = this;		
		return new Promise(function(resolve, reject) {
			var oppositeMapping = Array.prototype.filter.call(Object.values(availableValues), (el) => 
				el.kind === 'TFS' && el.key === mappingRow.tfsValue.key && el.id !== mappingRow.tfsValue.id
			);			
			if (oppositeMapping.length === 0) {
				resolve(null);
			} else {				
				self.removeMapping(availableValues, mappings, mappingRow, false).then(
					(data) => {						
						self.addMapping(mappingRow.jiraValue, oppositeMapping[0], data.available, data.mappings).then(
							(mappingData) => {
								resolve({
									mappings: mappingData,
									available: data.available
								});
							},
							(err) => reject(err)
						);
					},
					(err) => {
						reject(err);
					}
				);
			}
		});		
	}


	addMapping(jiraValue, tfsValue, availableValues, mappings) {
		var self = this;

		return new Promise(function(resolve, reject) {
			self.http.post(self.backendUrl + '/mappings/links/values', {
				JiraLinkType: jiraValue.key,
				TfsLinkType: tfsValue.key,
				IsLinkDirectionInverted: tfsValue.inverted
			}).then(
				(data) => {
					mappings.push(data.data);
					var enriched = self.hideListItemsAndEnrichMappings(mappings, availableValues);
					resolve(enriched.mappings);
				},
				(err) => {
					reject(err);
				}
			)
		});
	}

	removeMapping(availableValues, mappings, mappingObject, callHideItems) {
		var self = this;
		return new Promise(function(resolve, reject) {
			self.http.delete(self.backendUrl + '/mappings/links/values/' + mappingObject.Id).then(
				(data) => {
					mappings = Array.prototype.filter.call(mappings, (el) => el.Id !== mappingObject.Id);
					if (callHideItems) {
						resolve(self.hideListItemsAndEnrichMappings(
							mappings,
							availableValues
						));
					} else {
						resolve({
							mappings: mappings,
							available: availableValues
						})
					}
				},
				(err) => {
					reject(err);
				}
			)
		});
	}
}



