<style scoped>
    .spinner {
        display: block;
        background-position-y: 150px;
    }
</style>
<template>
    <div>
        <h1>Links Mapping</h1>
        <div class="tfs4jira-mapping-container">
            <link-mapping-selects v-bind:available-values="getAvailableValues()" v-bind:map-link-types="addMapping"></link-mapping-selects>
            <div class="tfs4jira-mapping-no-mapped-values-container" v-show="(mappings.length === 0)">
                <span class="tfs4jira-mapping-no-mapped-values">
                    Please select JIRA and TFS link types that you want to map.
                </span>
            </div>
            <table class="tfs4jira-mapping-table" style="display: table;">
                <tbody>
                    <link-mapping-row v-for="row in mappings" v-bind:mapping="row" v-bind:switchMapping="switchTfsMapping" :key=row.Id v-bind:remove="removeMapping"></link-mapping-row>
                </tbody>
            </table>
            <div class="spinner" v-show="showLoader === true"></div>
        </div>
        <div class="spinner" v-show="showLoader === true"></div>
    </div>
</template>
<script>
    import axios from 'axios';
    import contextPath from '../context-path';
    import Shared from '../shared';
    import BaseComponent from './BaseComponent';
    import LinkMappingSelects from './LinkMappingSelects';
    import LinkMappingRow from './LinkMappingRow';
    import LinkMappingService from './LinkMappingService.js';
    import LinkMappingMockService from './LinkMappingMockService.js';

    export default {
        extends: BaseComponent,
        props: [
            'profileGuid', 'loadOnInit'
        ],

        data() {
            return {
                showLoader: true,
                availableValues: {},
                mappings: [],
                mountCallback: function(){} // its for unit tests purposes
            }
        },

        methods: {
            getService() {                
                return new LinkMappingService(contextPath + '/api/profiles/' + this.profileGuid, axios);
            },

            handleComponentNotification(notification) {
                this.initComponent();
            },
            
            getAvailableValues() {
                return {
                    TfsValues: Array.prototype.filter.call(
                        Object.values(this.availableValues), value => value.visible && value.kind === 'TFS'
                    ),
                    JiraValues: Array.prototype.filter.call(
                        Object.values(this.availableValues), value => value.visible && value.kind === 'JIRA'
                    )
                };
            },

            initComponent() {
                this.showLoader = true;

                this.getService().getData().then(
                    (data) => {
                        this.availableValues = data.available;
                        this.mappings = data.mappings;
                        this.showLoader = false;
                        this.mountCallback();
                    },
                    (err) => this.handleError(err)
                );
            },

            switchTfsMapping(mappingObject) {
                mappingObject.duringProcessing = true;
                this.getService().switchTfsMapping(mappingObject, this.mappings, this.availableValues).then(
                    (data) => {
                        if (data !== null) {
                            this.mappings = data.mappings;
                            this.available = data.available;
                        }
                    },
                    (err) => this.handleError(err)
                )
            },

            addMapping(jiraValue, tfsValue) {
                this.getService().addMapping(jiraValue, tfsValue, this.availableValues, this.mappings).then(
                    (data) => {
                        this.mappings = data;
                    }, 
                    (err) => this.handleError(err)
                );
            },

            removeMapping(mappingObject) {
                mappingObject.duringProcessing = true;
                this.getService().removeMapping(this.availableValues, this.mappings, mappingObject, true).then(
                    (data) => {
                        this.mappings = data.mappings;
                        this.availableValues = data.available;
                    },
                    (err) => this.handleError(err)
                )
            },

            handleError(error) {
                var message = "";
                if (error.response !== undefined && error.response.data !== undefined && error.response.data.Message !== undefined) {
                    message = error.response.data.Message;
                } else if (error.message !== undefined) {
                    message = error.message;
                } else {
                    message = "Unknown error occured";
                }
                this.shared.addMessageBoxItem('Operation failed', message);
                this.showLoader = false;
            }
        },

        components: {
            LinkMappingSelects,
            LinkMappingRow
        },

        mounted() {
            if (this.loadOnInit !== undefined && this.loadOnInit === "true") {
                this.initComponent();
            }
        }
    }

</script>


