import axios from 'axios';
import Vue from 'vue'
import Service from './LinkMappingService'

export default class MockService extends Service  {
	constructor(backendUrl, http) {
		super(backendUrl, http);

		this.mappings = {
			'get': {
				'/mappings/links/availableValues': {
					data: {
						"JiraValues": [
							{"JiraLinkType":10000,"LabelA":"is blocked by","LabelB":"blocks"},
							{"JiraLinkType":10001,"LabelA":"is cloned by","LabelB":"clones"},
							{"JiraLinkType":10002,"LabelA":"is duplicated by","LabelB":"duplicates"}
						],
						
						"TfsValues":[
							{"TfsLinkType":"Microsoft.VSTS.TestCase.SharedParameterReferencedBy","LabelA":"References","LabelB":"Referenced By"},
							{"TfsLinkType":"Microsoft.VSTS.Common.TestedBy","LabelA":"Tests","LabelB":"Tested By"},
						]
					}
				},
				'/mappings/links': {
					data: {
						"LinkTypesMappings": [{
							"Id": 2,
							"JiraLinkType": 10000,
							"TfsLinkType": "Microsoft.VSTS.TestCase.SharedParameterReferencedBy",
							"IsLinkDirectionInverted": false
						}],
						"Id": 1
					}
				}
			},
			post: {
				'/mappings/links/values': {
					data: {
						"Id": 3,
						"JiraLinkType": 10002,
						"TfsLinkType": "Microsoft.VSTS.Common.TestedBy",
						"IsLinkDirectionInverted": false
					}
				}
			},
			delete: {
				'/mappings/links/values/2': {}
			}
		};

		var self = this;

		this.http = {
			get: function(url) {
				return new Promise(function(resolve, reject){
					resolve(
						self.getTestDataForUrl(url.replace(self.backendUrl, ''), self.mappings.get)
					);
				});
			},
			post: function(url, data) {
				return new Promise(function(resolve, reject){
					resolve(
						self.getTestDataForUrl(url.replace(self.backendUrl, ''), self.mappings.post)
					);
				});
			},
			delete: function(url) {
				return new Promise(function(resolve, reject){
					resolve(
						self.getTestDataForUrl(url.replace(self.backendUrl, ''), self.mappings.delete)
					);
				});
			}
		}
	}

	// Later we may implement here more logic
	getTestDataForUrl(url, dataMappings) {
		return dataMappings[url];
	}

	getSampleJiraValue() {
		return {"JiraLinkType":10001,"LabelA":"is cloned by","LabelB":"clones"};
	}

	getSampleTfsValue() {
		return {"TfsLinkType":"Microsoft.VSTS.Common.TestedBy","LabelA":"Tests","LabelB":"Tested By"};
	}
}


