<script>
    import contextPath from '../context-path';
    import Shared from '../shared';
    export default {
        data() {
            return {
                shared: Shared
            }
        },

        computed: {
            componentNotification: function() {
                return this.shared.componentNotification;
            }
        },

        watch: {
            componentNotification: function() {
                if (this.componentNotification.component !== undefined &&
                    this.componentNotification.component === this.$options._componentTag) {
                    this.handleComponentNotification(this.componentNotification);
                }
            }
        },

        methods: {
            handleComponentNotification(notification) {

            }
        }
    }
</script>


